<template>
    <el-dialog v-if="showDialog" class="together-detail"  :visible="showDialog" @close="onClose()" width="1000">
        <div class="title" slot="title">
            {{step==1?'交換物品詳情':'交換資訊確認'}}
        </div>
        <div class="item-info" v-if="step == 1">
            <div class="img-container">
                 <img :src="curItem.img?curItem.img:defaultimg" alt="">
            </div>
            <div class="category-group">
                <p class="category">{{curItem.category}}</p>
                <p class="group-name">{{curItem.groupName}}</p>
            </div>
            <div class="item-title">{{curItem.itemTitle}}</div>
            <div class="desc">{{curItem.desc}}</div>
            <p class="note">主揪 {{curItem.creator}} 截止日期 {{curItem.expired_time}}</p>
            <el-form class="apply-form" >
                <p v-if="curItem.rule === 'C' || curItem.rule === 'D'">多帶區</p>
                <el-checkbox-group v-model="checkListA">
                    <div v-for="(item,i) in curItem.itemListA" :key="i" >
                      <el-checkbox v-if="item[1]>0" :label="item"  >
                            <p >{{item[0]}} - {{item[2]}}/1</p> 
                            <el-select v-model="curNum[i]" placeholder="请選擇數量" v-if="checkListA.find(ele=>ele[0] === item[0])" @change="selectNum(i)">
                                <el-option v-for="(num,i) in Number(item[1])" :value="num" :key="i"></el-option>
                            </el-select>
                        </el-checkbox>
                    </div>
                        
                </el-checkbox-group>
            </el-form>
            <el-form class="apply-form" v-if="curItem.rule === 'C' || curItem.rule === 'D'">
                <p>單帶區</p>
                <el-checkbox-group v-model="checkListB">
                    <div v-for="(item,i) in curItem.itemListB" :key="i" >
                      <el-checkbox v-if="item[1]>0" :label="item"  >
                            <p >{{item[0]}} - {{item[2]}}/1</p> 
                            <el-select v-model="curNum[i]" placeholder="请選擇數量" v-if="checkListB.find(ele=>ele[0] === item[0])" @change="selectNum(i)">
                                <el-option v-for="(num,i) in Number(item[1])" :value="num" :key="i"></el-option>
                            </el-select>
                        </el-checkbox>
                    </div>
                        
                </el-checkbox-group>
            </el-form>
        </div>
       <div  class="check-form" v-if="step == 2">
        <p>
          <span class="form-title">交換物品</span>  
          <span class="category">{{curItem.itemTitle}}</span>
        </p>
        <p>
            <span class="form-title">擁有成員</span>
        </p>
      <p class="form-title">填寫交換訊息</p>
      <el-input type="textarea" v-model="message.postMessage" placeholder="請填入訊息"></el-input>
      <div class="warning-text"><i class="el-icon-warning"></i>點擊送出即提交申請，若隨意取消交易可能會影響您的評價</div>
    </div>
    <span slot="footer" class="dialog-footer">
				<el-button @click="onClose()">取 消</el-button>
				<el-button type="primary" @click="submit()">{{ step == 1?"下一步":"確認送出"}}</el-button>
			</span>
  </el-dialog>
</template>

<script>
import { mapState } from 'vuex'
import { applyTogetherItem  } from '../../../api/together'; 
export default {
    props: {
        showDialog: {
            type: Boolean,
            require: true
        },
        curItem:{

        }
    },
    data(){
		return {
            step: 1,
            defaultimg:require('../../../assets/default/doll.jpeg'),
            checkListA:[],
            checkListB:[],
            curNum:[],
            applyListA:[],
            applyListB:[],
            form:{
                msg:""
            },
             message:{
                fromUser:this.userName,
                toUser:this.curItem?this.curItem.creator:'',
                relatedItem: this.curItem?this.curItem.itemId:'',
                postMessage:"",
            }
		}
	},
     computed:{
      ...mapState({
            userImg: state => state.user.userImg,
            userName: state => state.user.userName,
        }),
    },
     watch:{
      showDialog(newValue){
        if (newValue){
          this.checkListA = []
          this.checkListB = []
          this.curNum = []
          this.applyListA = JSON.parse(JSON.stringify(this.curItem.itemListA)) 
          this.applyListB = JSON.parse(JSON.stringify(this.curItem.itemListB)) 
          this.step = 1
          this.message = {
                 fromUser:this.userName,
                 toUser:this.curItem?this.curItem.creator:'',
                 relatedItem: this.curItem?this.curItem.itemId:'',
                    postMessage:"",
            }
        }
      }
    },
    methods:{
        onClose(){
            this.$emit('update:showDialog', false)
        },
        selectNum(i){
            this.applyListA[i][1] = this.curNum[i]

        },
        submit(){
            if (this.step == 1){
                this.step = 2
                let str = ''
                for (let i=0;i<this.applyListA.length;i++){
                    if (this.applyListA[i][1] === this.curItem.itemListA[i][1]){
                        this.applyListA[i][1] = 0
                    }
                    if (this.applyListA[i][1]>0){
                        str+=`${this.applyListA[i][0]}x${this.applyListA[i][1]}`
                    }
                }
                for (let i=0;i<this.applyListB.length;i++){
                    if (this.applyListB[i][1] === this.curItem.itemListB[i][1]){
                        this.applyListB[i][1] = 0
                    }
                    if (this.applyListB[i][1]>0){
                        str+=`${this.applyListB[i][0]}x${this.applyListB[i][1]}`
                    }
                }
                this.message.postMessage = `您好，想拆售${str}，謝謝`
                return 
            }
            let params = {
                itemId: this.curItem.itemId,
                itemListA: this.applyListA,
                itemListB: this.applyListB,
                applier: this.userName,
                message: this.message
            } 
            applyTogetherItem(params).then(res => {
              console.log(res.data);
              this.$emit('success')
                if (res) {
                    }
                }).catch(err => {
                    console.log(err);
                });
            this.$emit('update:showDialog', false)
        }
    }
}
</script>

