<template>
 <div>
	 <div class="together-item-container">
		 <div class="together-item-row">
			 <div class="together-item-wrapper"  v-for="(item,i) in itemList" :key="i" @click="openTogetherDetail(item)">
				<div class="together-item" v-if="!isApply">
					<div class="img-container"><img :src="item.img?item.img:defaultimg" alt=""></div>
					<div class="category-container">
						<p class="group"><span class="category">{{item.category}}</span><span class="group-name">{{item.groupName}}</span></p>
						<div class="date">期限 {{item.expired_time}}</div>
					</div>
					<div class="title">
						{{item.itemTitle}}
					</div>
					<div class="desc">缺 <span v-for="(item,i) in item.itemListA.concat(item.itemListB)" :key="i"><span v-if="item[1]>0">{{item[0]}} </span></span></div>
					<div class="user">
						<div class="img"><img src="../../../assets/user.jpg" alt=""></div>
						<div class="loginname">{{item.creator}}</div>
					</div>
				</div>
				<div class="together-item" v-else>
					<div class="img-container"><img :src="item.img?item.togetherInfo.img:defaultimg" alt=""></div>
					<div class="category-container">
						<p class="group"><span class="category">{{item.togetherInfo.category}}</span><span class="group-name">{{item.groupName}}</span></p>
						<div class="date">期限 {{item.togetherInfo.expired_time}}</div>
					</div>
					<div class="title">
						{{item.togetherInfo.itemTitle}}
					</div>
					<div class="desc">缺 <span v-for="(item,i) in item.togetherInfo.itemList" :key="i"><span v-if="item[1]>0">{{item[0]}} </span></span></div>
					<div class="user">
						<div class="img"><img src="../../../assets/user.jpg" alt=""></div>
						<div class="loginname">{{item.togetherInfo.creator}}</div>
					</div>
				</div>
			</div>
		 </div>
	</div>
	<togetherDetail :showDialog.sync="showTogetherDetail" :curItem="curItem" @success="success"></togetherDetail>
	<togetherMessage :showDialog.sync="showExchangeMessage" :togetherItem ="curItem"></togetherMessage>
 </div>
	
</template>

<script>
import togetherMessage from './togetherMessage.vue'
import togetherDetail from './togetherDetail'
export default {
	name:'togetherItem',
	components:{
        togetherDetail,
		togetherMessage
    },
	props:{
		itemList:{
			type: Array,
			default: []
		},
		isList:{
			type:Boolean,
			default:true
		},
		isApply:{
			type: Boolean,
			default: false,
		},
	},
	data(){
		return {
			curItem:null,
			defaultimg:require('../../../assets/default/doll.jpeg'),
			showTogetherDetail: false,
			showExchangeMessage: false,
		}
		
	},
	methods:{
		openTogetherDetail(item){
			if (this.isApply){
				this.curItem = item.togetherInfo
			} else {
				this.curItem= item
			}
			if (this.isList){
				this.showTogetherDetail = true
			} else {
				this.showExchangeMessage = true
			}
            
        },
		success(){
			this.$emit('success')
		}
	}

}
</script>

